<template>
  <div style="padding: 20px; width: 100%; height: 73%">
    <breadcrumb-component
      :breadcrumb-path="[{ title: '일정' }]"
      breadcrumb-title=" 물 관리 일정"
      :is-calendar="true"
    ></breadcrumb-component>

    <div
      style="
        display: flex;
        margin: 20px 0 0 0;
        align-items: center;
        font-family: 'NotoSansKR-Medium';
      "
    >
      <div>저수지</div>
      <div style="margin: 0 0 0 5px">
        <el-select
          v-model="selectedReservoirForCalendar"
          @change="changeCalendar()"
        >
          <el-option value="전체" label="전체">전체</el-option>
          <el-option
            v-for="reservoir in reservoirList"
            :key="reservoir.reservoirId"
            :label="reservoir.reservoirName"
            :value="reservoir.reservoirName"
          ></el-option>
        </el-select>
      </div>
    </div>

    <el-dialog
      v-model="showDialog"
      :show-close="true"
      center
      align-center
      draggable
      :selectable="true"
      class="calendar-container"
      @close="closeModal()"
    >
      <template #header>
        <div class="calendar-header">물 공급 일정 추가</div>
      </template>

      <template #default>
        <div style="display: flex">
          <div>
            <div>일정</div>
            <div style="margin: 3px 0 0 0">
              <el-date-picker
                type="date"
                v-model="eventDate"
                placeholder="공급일정 선택"
              ></el-date-picker>
            </div>
          </div>
          <div style="margin: 0 0 0 10px">
            <div>저수지</div>
            <div style="margin: 3px 0 0 0">
              <el-select
                :disabled="fixSelectedReservoir"
                style="width: 220px"
                v-model="selectedReservoir"
                clearable
                placeholder="저수지를 선택해주세요."
              >
                <el-option
                  v-for="reservoir in reservoirList"
                  :key="reservoir.reservoirId"
                  :label="reservoir.reservoirName"
                  :value="reservoir.reservoirName"
                ></el-option
              ></el-select>
            </div>
          </div>
        </div>
        <div style="margin: 20px 0 0 0; text-align: right">
          <el-button @click="closeDialog()">취소</el-button>
          <el-button color="#0d4ba9" @click="updateCalendar()">저장</el-button>
        </div>

        <!-- confirm modal -->
        <el-dialog
          v-model="showConfirmDialog"
          :show-close="true"
          center
          align-center
          draggable
          style="width: 300px; border-radius: 8px"
          @keypress.enter="confirmSave()"
        >
          <template #header>
            <div>일정추가</div>
          </template>

          <div
            v-if="isError"
            style="
              display: flex;
              color: #ff0000;
              justify-content: center;
              align-items: center;
            "
          >
            일자 또는 저수지를 선택해주세요.
          </div>
          <div
            v-else
            style="
              display: flex;
              color: #424242;
              justify-content: center;
              align-items: center;
            "
          >
            저장하시겠습니까?
          </div>
          <div style="text-align: center; margin: 30px 0 0 0">
            <el-button @click="showConfirmDialog = false">취소</el-button>
            <el-button
              color="#0d4ba9"
              :disabled="isError"
              @click="confirmSave()"
              >저장</el-button
            >
          </div>
        </el-dialog>
      </template>
    </el-dialog>

    <el-dialog
      v-model="showEditDialog"
      align-center
      class="calendar-container"
      draggable
      :show-close="true"
    >
      <template #header>
        <div class="calendar-header" style="text-align: center">
          물 공급 일정 상세정보
        </div>
      </template>

      <template #default>
        <div class="calendar-info-detail">
          <div style="display: flex">
            <div>일자</div>
            <div>{{ formedEventDate }}</div>
          </div>
          <div style="display: flex">
            <div>저수지명</div>
            <div>{{ selectedReservoir }}</div>
          </div>
          <div style="display: flex">
            <div>저수율</div>
            <div>90%</div>
          </div>
          <div style="display: flex">
            <div>예측 저수율</div>
            <div>91%</div>
          </div>
        </div>
        <div
          style="
            font-family: 'NotoSansKR-Regular';
            display: flex;
            justify-content: space-between;
            margin: 10px 0 0 0;
          "
        >
          <el-button color="red" plain @click="deleteEvent()"
            >일정삭제</el-button
          >
          <el-button color="#0d4ba9" @click="showEditDialog = false"
            >확인</el-button
          >
        </div>

        <!-- confirm modal -->
        <el-dialog
          v-model="showDeleteConfirmDialog"
          :show-close="true"
          center
          align-center
          draggable
          style="width: 300px; border-radius: 8px"
          @keypress.enter="confirmSave()"
        >
          <template #header>
            <div>일정삭제</div>
          </template>

          <div style="text-align: center">삭제하시겠습니까?</div>

          <div style="text-align: center; margin: 30px 0 0 0">
            <el-button @click="showDeleteConfirmDialog = false">취소</el-button>
            <el-button color="red" @click="confirmDeleteEvent()"
              >삭제</el-button
            >
          </div>
        </el-dialog>
      </template>
    </el-dialog>

    <div
      ref="calendar"
      style="
        height: 70%;
        width: 100%;
        margin: 10px 0 0 0;
        font-family: 'NotoSansKR-Regular';
      "
    ></div>
  </div>
</template>

<script>
import BreadcrumbComponent from "../common/BreadcrumbComponent.vue";
import { Calendar } from "fullcalendar";
import reservoirList from "../../modules/hard_data/reservoir.json";
import moment from "moment";

export default {
  name: "CalendarContainer",

  components: {
    BreadcrumbComponent,
  },

  data() {
    return {
      showDeleteConfirmDialog: false,
      showEditDialog: false,
      selectedEventObject: {},

      isError: false,
      errorMessage: "",
      selectedReservoirForCalendar: "전체",
      reservoirList,
      fixSelectedReservoir: false,

      showConfirmDialog: false,

      showDialog: false,
      selectedReservoir: "",

      eventDate: undefined,

      calendar: undefined,
      calendarOptions: {
        droppable: true,
        height: "100%",
        locale: "ko",
        editable: true,
        headerToolbar: {
          left: "prev next today",
          center: "title",
          right: "dayGridMonth timeGridWeek timeGridDay addEvent",
        },
        buttonText: {
          today: "오늘",
          month: "월",
          week: "주",
          day: "일",
          addEvent: "일정추가",
        },
        customButtons: {
          addEvent: {
            text: "일정 추가",
            click: () => {
              // console.log('click')
              if (this.selectedReservoirForCalendar !== "전체") {
                this.selectedReservoir = this.selectedReservoirForCalendar;
              } else {
                this.selectReservoir = "";
              }
              this.eventDate = "";
              this.showDialog = true;
            },
          },
          selectReservoir: {
            text: "저수지",
          },
        },
        dayMaxEventRows: true,
        views: {
          timeGrid: {
            dayMaxEventRows: 3,
          },
        },
        events: [],
        eventClick: (info) => {
          this.showEditDialog = true;
          console.log("---info", info.event._def.publicId);

          this.selectedEventObject = info;
          this.selectedEventId = info.event._def.publicId;
        },
        dateClick: (info) => {
          this.calendarInfo = info;
          this.selectDate();
        },
      },

      newEventList: [],

      formedEventDate: "",

      calendarEvents: [], // original ones
      eventsForDisplay: [], // events for display

      calendarInfo: undefined,
      selectedEventId: "",
    };
  },

  mounted() {
    this.initCalendar();
  },

  methods: {
    deleteEvent() {
      this.showDeleteConfirmDialog = true;
    },

    confirmDeleteEvent() {
      let eventList = this.calendar.getEventById(this.selectedEventId);
      eventList.remove();
      this.showDeleteConfirmDialog = false;
      this.showEditDialog = false;
    },

    changeCalendar() {
      this.calendar.destroy();

      this.initCalendar();

      if (this.selectedReservoirForCalendar !== "전체") {
        this.selectedReservoir = this.selectedReservoirForCalendar;
        this.newEventList.filter((event) => {
          if (event.title.includes(this.selectedReservoirForCalendar)) {
            this.calendar.addEvent({
              start: event.start,
              end: event.end,
              title: event.title,
              id: this.newEventList.length + 1,
            });
          }
        });

        this.fixSelectedReservoir = true;
      } else {
        this.displayAllCalendarEvent();
      }

      // this.selectedReservoirForCalendar = event
    },

    displayAllCalendarEvent() {
      for (let i = 0; i < this.newEventList.length; i++) {
        this.calendar.addEvent({
          start: this.newEventList[i].start,
          end: this.newEventList[i].end,
          title: this.newEventList[i].title,
          id: i,
        });
      }
    },

    initCalendar() {
      let calendarDiv = this.$refs.calendar;

      this.calendar = new Calendar(calendarDiv, this.calendarOptions);

      this.calendar.render();
      this.fixSelectedReservoir = false;
    },

    selectDate() {
      this.showDialog = true;
      this.eventDate = this.calendarInfo.date;
      this.calendarInfo.dayEl.style.backgroundColor = "#c6e2ff";
    },

    updateCalendar() {
      this.newEventList.push({
        start: this.eventDate,
        end: this.eventDate,
        title: this.selectedReservoir,
      });

      this.showConfirmDialog = true;
    },

    closeDialog() {
      this.showDialog = false;
    },

    closeModal() {},

    confirmSave() {
      this.calendar.addEvent({
        start: this.eventDate,
        end: this.eventDate,
        title: this.selectedReservoir,
        id: this.newEventList.length + 1,
      });

      this.formedEventDate = moment(this.eventDate).format(
        "YYYY-MM-DD a hh:mm:ss"
      );

      this.showConfirmDialog = false;
      this.showDialog = false;
    },
  },

  computed: {
    detectDialogClosingEvent() {
      return this.showDialog;
    },
  },

  watch: {
    detectDialogClosingEvent(data) {
      if (data === false) {
        if (this.calendarInfo !== undefined) {
          this.calendarInfo.dayEl.style.backgroundColor = "#fff";
        }
      } else {
        if (this.fixSelectedReservoir === false) {
          this.selectedReservoir = "";
        }
      }
    },

    showConfirmDialog(data) {
      console.log("data", data);
      if (data === true) {
        if (this.selectedReservoir === "" || this.eventDate === "") {
          this.isError = true;
        } else {
          this.isError = false;
        }
      }
    },
  },
};
</script>

<style>
@import "../../assets/css/dialog.css";

:root {
  /* --fc-button-text-color: rgb(13, 75, 169); */
  /* --fc-button-text-color: #fff; */
  --fc-button-bg-color: rgb(13, 75, 169);
  --fc-button-border-color: rgb(13, 75, 169);

  --fc-button-hover-bg-color: #ecf5ff;
  --fc-button-hover-border-color: #c6e2ff;

  --fc-button-active-border-color: rgb(13, 75, 169);
  --fc-button-active-bg-color: #fff;
  /* --fc-button-active-bg-color: #ecf5ff; */
  /* --fc-button-active- */
  --fc-event-text-color: #409eff;
}

.fc .fc-button:hover {
  color: #409eff;
}

/* .fc .fc-button-active::before {
  color: rgb(13, 75, 169) !important;
} */

.fc .fc-button-active {
  color: rgb(13, 75, 169) !important;
}

.fc .fc-button:disabled {
  opacity: 1;
  background-color: #fff;
  border-color: #aaa;
  color: #aaa;
}

.fc .fc-button:disabled:hover {
  cursor: not-allowed;
}
</style>
